
.back-btn {
    height: 32px !important;
    width: 88px;
    border-radius: 16px;
    border: 1px solid #B7B7BC;
    cursor: pointer;
    color: #40404D;
    background: #F6F6F9;
    margin-top: 8px;
}

.proof-title {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 32px;
    line-height: 40px;
    text-align: center;
    color: #101020;
    line-height: 48px;
}

@media screen and (max-width: 576px) {
    
    .proof-main {
        width: 100%;
        font-family: 'Montserrat' !important;
        font-style: normal !important;
        margin-top: -16px;
    }
    .proof-context {
        max-width: 960px;
        align-self: center;
        margin:0 auto;
    }
    
    .proof-top-btn {
        width: 100%;
    }
    
    .proof-top-title {
        width: 100%;
        text-align: center;
    }
}

@media screen and (min-width: 576px) {
    .proof-top {
        height: 48px;
        display: flex;
    }
    .proof-main {
        width: 100%;
        padding-inline: 24px;
        font-family: 'Montserrat' !important;
        font-style: normal !important;
    }
    .proof-context {
        max-width: 960px;
        align-self: center;
        margin:0 auto;
        padding-top: 6px;
    }
    .proof-top-btn {
        float:left;
    }
    
    .proof-top-title {
        flex: 1;
        float:left;
        text-align: center;
        padding-right: 88px;
    }
}