
@media screen and (max-width: 576px) {
    .detail-header {
        background-color: #FFFFFF;
        border-radius: 8px;
        padding: 16px;
        width: 100%;
    }
    .detail-header-info-title {
        width: 100%;
        color: #40404D;
        font-size: 16px;
        font-weight: 400;
        vertical-align: middle;
        padding-block: 6px;
    }
    
    .detail-header-info-value {
        padding-block: 6px;
        width: 100%;
        font-weight: 700;
        font-size: 16px;
        text-align: left;
        color: #101020;
        padding-left: 28px;
        word-wrap: break-word;
        word-break: normal;
    }
    .detail-header-info {
        height: auto;
    }
    .detail-header-info-value-suffix {
        color: #40404D;
        font-weight: 400;
        margin-left: 8px;
        font-size: 10px;
    }
}

@media screen and (min-width: 576px) {
    .detail-header {
        background-color: #FFFFFF;
        border-radius: 8px;
        padding: 16px;
        min-width: 960px;
    }
    .detail-header-info {
        height: 40px;
        line-height: 40px;
    }
    .detail-header-info-title {
        float: left;
        color: #40404D;
        font-size: 16px;
        font-weight: 400;
        vertical-align: middle;
    }
    
    .detail-header-info-value {
        float: right;
        font-weight: 700;
        font-size: 16px;
        line-height: 40px;
        text-align: right;
        color: #101020;
    }
    .detail-header-info-value-suffix {
        color: #40404D;
        font-weight: 400;
        margin-left: 8px;
    }
}

.detail-title {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 28px;
    color: #40404D;
}